.about-me {
  width: 100%;

  text-align: center;

  color: #ffffff;
  background: rgb(0, 0, 0);

  @include mobile {
    text-align: left;

    color: #000000;
    background: #ffffff;
  }
}

.about-me__wrapper {
  max-width: 1440px;

  padding: 50px;
  margin: 0 auto;

  @include mobile {
    max-width: none;

    padding: 15px;
  }
}

.about-me__title {
  margin: 0 0 20px;
}

.about-me__text {
  margin: 0 0 20px;
}

.about-me__list {
  text-align: start;

  display: flex;
  flex-flow: row wrap;
  gap: 20px 0;

  margin: 0;

  @include mobile {
    padding: 0 0 0 19px;
  }
}
