.footer {
  width: 100%;

  color: white;
  background: black;

  padding: 50px;

  @include mobile {
    color: #000000;
    background: #ffffff;

    padding: 15px;
  }
}

.footer__wrapper {
  width: 100%;
  max-width: 1440px;

  display: flex;
  justify-content: space-between;

  margin: 0 auto;

  @include mobile {
    max-width: none;

    flex-flow: column wrap;
  }
}
