.experience {
  width: 100%;

  text-align: center;

  @include mobile {
    text-align: left;

    color: #ffffff;
    background: #000000;
  }
}

.experience__wrapper {
  max-width: 1440px;

  padding: 50px;
  margin: 0 auto;

  @include mobile {
    max-width: none;

    padding: 15px;
  }
}

.experience__list {
  list-style-type: none;
  text-align: left;

  padding: 0;
}


.experience__item-begin {
  color: deepskyblue;
}
.experience__item-link {
  text-decoration: none;
  color: rgb(255, 0, 200);
}
