.my-targets {
  width: 100%;

  text-align: center;

  @include mobile {
    text-align: initial;

    color: #ffffff;
    background: #000000;
  }
}

.my-targets__wrapper {
  max-width: 1440px;

  padding: 50px;
  margin: 0 auto;

  @include mobile {
    max-width: none;

    padding: 15px;
  }
}

.my-targets__link {
  text-decoration: none;

  color: rgb(255, 0, 200);
}

.my-targets__item {
  color: deepskyblue;
}
