@include font("Roboto", "Roboto-Regular", 400);
@include font("Roboto", "Roboto-Medium", 400);
@include font("Roboto", "Roboto-Bold", 700);

.page {
  width: 100%;
  height: 100%;

  font-family: "Roboto", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;

  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;

  color: black;

  margin: 0;

  scroll-behavior: smooth;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;

  border: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;

  padding: 0;
  margin: -1px;
}

.hide {
  display: none;
}

figure {
  width: 100%;
  height: 100%;

  display: block;

  margin: 0;
}
