.preview {
  width: 100%;
  max-width: 1440px;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 50px;
  margin: 0 auto;

  @include mobile {
    max-width: none;

    background: #000000;

    padding: 15px;
  }
}

.preview__title {
  width: 80%;

  font-size: 26px;
  line-height: 30px;
  text-align: center;

  color: orange;

  margin: 0;

  @include mobile {
    width: 100%;

    font-size: 20px;
  }
}

